@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
:root {
  --color-basic: #92a1aa;
  --color-darken: #004d40;
  --color-border: #38827b;
  --color-border-darken: #78909c;
  --color-dark: #000000;
  /* --color-red-opacity: rgba(6, 234, 17, 0.5);
  --color-green-opacity: rgba(218, 11, 80, 0.5); */

  /* --shadow-dark: 5px 5px 7px rgba(0, 0, 0, 0.25);
  --shadow-light: -5px -5px 7px rgba(255, 255, 255, 0.3); */
  --shadow-in: inset 5px 5px 7px rgba(0, 0, 0, 0.25),
    inset -5px -5px 7px rgba(255, 255, 255, 0.3);
  --shadow-out: 5px 5px 7px rgba(0, 0, 0, 0.25),
    -5px -5px 7px rgba(255, 255, 255, 0.3);
  --shadow-out-in: 5px 5px 7px rgba(0, 0, 0, 0.25),
    -5px -5px 7px rgba(255, 255, 255, 0.3),
    inset 5px 5px 7px rgba(0, 0, 0, 0.25),
    inset -5px -5px 7px rgba(255, 255, 255, 0.3);
  --shadow-convex: 5px 5px 7px 0 rgba(255, 255, 255, 0.3) inset,
    -3px -3px 5px 0 rgba(0, 0, 0, 0.25) inset;

  --background-concave: linear-gradient(
    135deg,
    rgba(0, 0, 0, 0.22),
    rgba(255, 255, 255, 0.25)
  );
  --background-convex: linear-gradient(
    -45deg,
    rgba(0, 0, 0, 0.22),
    rgba(255, 255, 255, 0.25)
  );

  --border-radius-standart: 7px;
}
body {
  font-family: 'Montserrat', cursive, -apple-system, BlinkMacSystemFont,
    'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans',
    'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background-color: #92a1aa;

  background-color: var(--color-basic);
  padding: 0;
  margin: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.Section_section__YsQyR {
  text-align: center;
}

.FeedbackOptions_btn__24hyJ {
  border: none;
  cursor: pointer;
  width: 70px;
  height: 70px;
  padding: 10px;
  margin: 0 10px;
  box-shadow: var(--shadow-out);
  background: var(--background-convex);
  border-radius: var(--border-radius-standart);
  border: 1px solid var(--color-border);
}
.FeedbackOptions_btn__24hyJ:hover {
  background: var(--background-concave);
}
.FeedbackOptions_btn__24hyJ:active {
  box-shadow: var(--shadow-in);
}

.Statistics_container__2cmS9 {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 300px;
  margin: 0 auto;
  color: var(--color-darken);
}
.Statistics_positivePercentage__1ii-c,
.Statistics_results__30DcC {
  display: flex;
  justify-content: space-around;
  width: inherit;
  padding: 7px;
  border: 1px solid var(--color-border-darken);
  border-radius: var(--border-radius-standart);
  box-shadow: var(--shadow-out);
}

.Statistics_value__39mWR {
  color: var(--color-dark);
  font-weight: bold;
}
.Statistics_total__KiwL8 {
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  margin: 0;
  padding-top: 22px;
  width: 80px;
  height: 80px;
  border: 1px solid var(--color-border);
  box-shadow: var(--shadow-out);
  border-radius: 50%;
  background-color: var(--color-basic);
  position: relative;
}
.Statistics_results__30DcC {
  margin-bottom: -40px;
  padding-bottom: 40px;
}
.Statistics_positivePercentage__1ii-c {
  margin: -40px 0 0 0;
  padding: 60px 7px 20px;
}

